.header{position:fixed;top:0;left:0;width:100%;height:100px;z-index:100;background-color:var(--pure-white-color)}.header .wrap-page{display:flex;justify-content:space-between;align-items:center;padding-block:var(--space-block-xs)}.header .header__logo-img{display:block;height:auto;width:210px;margin-top:10px}.header .header__nav{display:none}@media(min-width: 768px){.header .header__nav{display:block}}.header .header__nav .header__nav-list{display:flex;gap:var(--space-block-xs);list-style:none;margin:0;padding:0}.header .header__nav .header__nav-list li .header__nav-link{font-size:var(--text-caption);font-weight:var(--font-weight-regular);color:var(--black-color);text-decoration:none;letter-spacing:.14px;transition:color .2s ease}.header .header__nav .header__nav-list li .header__nav-link:hover{color:var(--teal-green-color)}.header .header__nav .header__nav-list li .header__login-btn{font-size:var(--text-caption);padding:8px 16px;border:1px solid var(--theme-blue-color);border-radius:0px;color:var(--theme-blue-color);text-decoration:none;font-weight:var(--font-weight-regular)}.header .header__nav .header__nav-list li .header__login-btn:hover{background-color:var(--pure-white-color);color:var(--black-color)}.header .header__hamburger{display:flex;flex-direction:column;justify-content:center;gap:5px;width:30px;height:30px;background:none;border:none;cursor:pointer}.header .header__hamburger span{height:2px;width:100%;background-color:#000;transition:all .3s}.header .header__hamburger.open span:nth-child(1){transform:rotate(45deg) translateY(6px)}.header .header__hamburger.open span:nth-child(2){opacity:0}.header .header__hamburger.open span:nth-child(3){transform:rotate(-45deg) translateY(-6px)}@media(min-width: 768px){.header .header__hamburger{display:none}}.header .header__drawer{display:none;position:fixed;top:0;right:0;width:75%;max-width:300px;height:100vh;background-color:var(--pure-white-color);transform:translateX(100%);transition:transform .3s ease;padding:var(--space-block-sm);box-shadow:-4px 0 10px rgba(0,0,0,.1);z-index:101}.header .header__drawer.show{display:block;transform:translateX(0)}@media(min-width: 768px){.header .header__drawer{display:none !important}}.header .header__drawer .header__drawer-close{background:none;border:none;width:30px;height:30px;margin-bottom:var(--space-block-xs);display:flex;flex-direction:column;justify-content:center;gap:5px}.header .header__drawer .header__drawer-close span{height:2px;width:100%;background-color:#000}.header .header__drawer .header__drawer-close span:nth-child(1){transform:rotate(45deg) translateY(6px)}.header .header__drawer .header__drawer-close span:nth-child(2){opacity:0}.header .header__drawer .header__drawer-close span:nth-child(3){transform:rotate(-45deg) translateY(-6px)}.header .header__drawer .header__drawer-list{display:flex;flex-direction:column;gap:var(--space-block-xs);list-style:none;padding:0}.header .header__drawer .header__drawer-list li .header__drawer-link{font-size:var(--text-md);color:var(--theme-blue-color);text-decoration:none}.header .header__drawer .header__drawer-list li .header__drawer-link:hover{color:var(--teal-green-color)}.header .header__drawer .header__drawer-list li .header__drawer-login{font-size:var(--text-xs);padding:8px 16px;border:1px solid var(--theme-blue-color);border-radius:4px;color:var(--theme-blue-color);text-decoration:none}.header .header__drawer .header__drawer-list li .header__drawer-login:hover{background-color:var(--theme-blue-color);color:var(--pure-white-color)}